<template>
  <div id="editEmployee">
    <h3>Edit Employee</h3>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="input-field col s12">
          <input v-model="employee_id" required disabled type="text">
        </div>
      </form>
    </div>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="input-field col s12">
          <input v-model="name" required type="text">
        </div>
      </form>
    </div>
    <div class="row">
      <form @submit.prevent="updateEmployee" class="col s12">
        <div class="input-field col s12">
          <input v-model="dept" required type="text">
        </div>
      </form>
    </div>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="input-field col s12">
          <input v-model="position" required type="text">
        </div>
      </form>
    </div>
    <button @click="updateEmployee" type="submit" class="btn">Submit</button>
    <router-link to="/" class="btn grey">Cancle</router-link>
  </div>
</template>

<script>
import db from "./firebaseInit"
export default {
  name: 'editEmployee',
  data () {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  beforeRouteEnter(to, from, next) {
      db.collection("employees")
        .where('employee_id', '==', to.params.employee_id)
        .get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.employee_id = doc.data().employee_id;
              vm.name = doc.data().name;
              vm.dept = doc.data().dept;
              vm.position = doc.data().position;
            })
          })
        })
    },
    watch: {
      '$route': "fetchData"
    },
    methods: {
      fetchData() {
        db.collection("employees")
          .where('employee_id', '==', this.$route.params.employee_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.employee_id = doc.data().employee_id
              this.name = doc.data().name
              this.dept = doc.data().dept
              this.position = doc.data().position
            })
          })
      },
      updateEmployee() {
        db.collection("employees")
          .where('employee_id', '==', this.$route.params.employee_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.update({
                employee_id: this.employee_id,
                name: this.name,
                dept: this.dept,
                position: this.position
              })
              .then(() => {
                this.$router.push({name: 'viewEmployee', 
                params:{employee_id:this.employee_id}})
              })
            })
        })
      }
    }
  }
</script>


