<template>
  <div id="newEmployee">
    <h3>New Employee</h3>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="input-field col s12">
          <input v-model="employee_id" required type="text">
          <label>Employee ID#</label>
        </div>
      </form>
    </div>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="input-field col s12">
          <input v-model="name" required type="text">
          <label>Name</label>
        </div>
      </form>
    </div>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="input-field col s12">
          <input v-model="dept" required type="text">
          <label>Department</label>
        </div>
      </form>
    </div>
    <div class="row">
      <form @submit.prevent="saveEmployee" class="col s12">
        <div class="input-field col s12">
          <input v-model="position" required type="text">
          <label>Position</label>
        </div>
      </form>
    </div>
    <button @click="saveEmployee" type="submit" class="btn">Submit</button>
    <router-link to="/" class="btn grey">Cancle</router-link>
  </div>
</template>

<script>
import db from "./firebaseInit"
export default {
  name: "newEmployee",
  data () {
    return {
      employee_id: null,
      name: null,
      dept: null,
      position: null
    }
  },
  methods: {
    saveEmployee () {
      db.collection('employees').add({
        employee_id: this.employee_id,
        name: this.name,
        dept: this.dept,
        position: this.position
      })
      .then(docRef => {
        console.log('Client added: ', docRef.id)
        this.$router.push('/')
      })
      .catch(error => {
        console.error('Error adding employee: ', error)
      })
    }
  }
}
</script>


